<template>
  <div>
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      @load="onLoad"
      offset="50"
      :immediate-check="false"
    >
    <ArticleItem
      v-for="obj in list"
      :key="obj.art_id"
      :artObj="obj"
      @disLikeEV="disLikeFn"
      @reportEV="reportFn"
      @click.native="itemClickFn(obj.art_id)"
    ></ArticleItem>
    </van-list>
    </van-pull-refresh>
  </div>
</template>

<script>
import ArticleItem from '../../../components/ArticleItem.vue'
import { getAllArticleListAPI, dislikeArticleAPI, reportArticleAPI } from '@/api/index.js'
import Notify from '@/ui/Notify.js'
export default {
  data () {
    return {
      list: [],
      loading: false,
      finished: false,
      theTime: (new Date()).getTime(),
      isLoading: false
    }
  },
  props: {
    cid: Number
  },
  components: {
    ArticleItem
  },
  async created () {
    // 文章列表
    const res = await getAllArticleListAPI({
      channelid: this.cid,
      timestamp: this.theTime
    })
    this.list = res.data.data.results
    this.theTime = res.data.data.pre_timestamp
  },
  methods: {
    async getArticleListFn () {
      const res = await getAllArticleListAPI({
        channelid: this.cid,
        timestamp: this.theTime
      })
      console.log(res)
      this.list = [...this.list, ...res.data.data.results]
      this.theTime = res.data.data.pre_timestamp
      this.loading = false
      if (this.list.pre_timestamp === null) {
        this.finished = true
      }
      this.isLoading = false
    },
    // 底部加载事件方法
    onLoad () {
      if (this.list.length === 0) {
        this.loading = false
        return
      }
      this.getArticleListFn()
    },
    // 顶部 -下拉刷新方法
    async onRefresh () {
      this.list = []
      this.theTime = (new Date()).getTime()
      this.getArticleListFn()
    },
    // 反馈 - 不感兴趣
    async disLikeFn (id) {
      await dislikeArticleAPI({ artId: id })
      Notify({ type: 'success', message: '反馈成功' })
    },
    // 反馈 - 垃圾报告
    async reportFn (target, type) {
      await reportArticleAPI({ target, type })
      Notify({ type: 'success', message: '反馈成功' })
    },
    // 文章单元格 - 点击事件
    itemClickFn (id) {
      this.$router.push(`/detail?art_id=${id}`)
    }
  }
}
</script>

