<template>
  <div>
    <!-- 顶部导航栏 -->
    <div>
      <van-nav-bar fixed>
        <template #left>
          <img class="logo" src="@/assets/Logo.png" alt="" />
        </template>
        <template #right>
          <!-- 搜索放大镜 -->
          <van-icon name="search" size="0.48rem" color="#fff" @click="moveSearchPageFn"/>
        </template>
      </van-nav-bar>
    </div>
    <!-- Tab 标签页 -->
    <div class="main">
      <van-tabs v-model="channelId" sticky offset-top="1.226667rem" @change="channelChangeFn" animated>
        <van-tab v-for="obj in userChannelList" :key="obj.id" :title="obj.name" :name="obj.id">
          <ArticleList :cid="channelId"></ArticleList>
        </van-tab>
      </van-tabs>
    </div>
    <!-- 编辑频道图标 -->
    <van-icon name="plus" size="0.37333334rem" class="moreChannels" @click="showPopup" />
    <!-- 频道管理弹出层 -->
    <van-popup v-model="show" get-container="body" class="channel-popup" >
      <ChannelEdit
      :userChannelList="userChannelList"
      :unCheckChannelList="unCheckChannelList"
      @addChannelEV="addChannelFn"
      @removeChannelEV="removeChannelEV"
      @closeEV="closeFn"
      v-model="channelId"
      ref="editRef"></ChannelEdit>
      </van-popup>
  </div>
</template>

<script>
import { getUserChannelsAPI, getAllChannelsAPI, updateChannelsAPI, removeChannelsAPI } from '@/api/index.js'
import ArticleList from './components/ArticleList.vue'
import ChannelEdit from './ChannelEdit.vue'

export default {
  data () {
    return {
      channelId: 0,
      userChannelList: [],
      allChannelList: [],
      show: false,
      channelScrollTObj: {}
    }
  },
  async created () {
    // 频道列表
    const res = await getUserChannelsAPI()
    console.log(res)
    this.userChannelList = res.data.data.channels
    const res2 = await getAllChannelsAPI()
    this.allChannelList = res2.data.data.channels
    console.log(res2)
  },
  components: {
    ArticleList,
    ChannelEdit
  },
  methods: {
    // +号点击方法
    showPopup () {
      this.show = true
    },
    // 添加频道
    async addChannelFn (smallObj) {
      this.userChannelList.push(smallObj)
      //   const res = await updateChannelsAPI({
      //     channels: this.userChannelList
      //   })
      //   console.log(res)
      // }
      const newArr = this.userChannelList.filter(obj => obj.od !== 0)
      const theNewArr = newArr.map((obj, index) => {
        const newObj = { ...obj }
        delete newObj.name
        newObj.seq = index
        return newObj // 让map方法把新对象收集起来形成一个新对象
      })
      const res = await updateChannelsAPI({
        channels: theNewArr
      })
      console.log(res)
    },
    // 删除频道
    async removeChannelEV (channelObj) {
      // 第一种方式：把现在用户的数据，覆盖式发给后台
      const index = this.userChannelList.findIndex(obj => obj.id === channelObj.id)
      this.userChannelList.splice(index, 1)
      // 第二种：只调用删除的接口
      await removeChannelsAPI({
        target: channelObj.id
      })
    },
    // 关闭弹出层
    closeFn () {
      this.show = false
      this.$refs.editRef.isEdit = false
    },
    // 首页 -> 右上角放大镜点击事件 -> 跳转到搜索页面
    moveSearchPageFn () {
      this.$router.push('/search')
    },
    scrollFn () {
      this.$route.meta.scrollT = document.documentElement.scrollTop
      this.channelScrollTObj[this.channelId] = document.documentElement.scrollTop
    },
    // 频道切换方法
    channelChangeFn () {
      // 切回来的一瞬间高度为0，你设置滚动位置没用
      console.log(111)
      this.$nextTick(() => {
        document.documentElement.scrollTop = this.channelScrollTObj[this.channelId]
      })
    }
  },
  // 计算属性
  computed: {
    // 频道 - 获取添加更多频道的数据的方法
    // unCheckChannelList () {
    //   const newArr = this.allChannelList.filter(bigObj => {
    //     const index = this.userChannelList.findIndex(smallObj => {
    //       return smallObj.id === bigObj.id
    //     })
    //     // 如果找到了
    //     if (index > -1) {
    //       return false
    //     } else {
    //       return true
    //     }
    //   })
    //   return newArr
    // }

    // 优化写法
    unCheckChannelList () {
      return this.allChannelList.filter(bigObj => this.userChannelList.findIndex(smallObj => smallObj.id === bigObj.id) === -1)
    }
  },
  activated () {
    window.addEventListener('scroll', this.scrollFn)
    // window 和 document ,监听网页滚动事件
    // html标签过获取scrollTop，滚动的距离，和设置滚动的位置
    // 立刻设置，滚动条的位置
    document.documentElement.scrollTop = this.$route.meta.scrollT
  },
  deactivated () {
    // 切换时释放滚动条方法，不然冲突
    window.removeEventListener('scroll', this.scrollFn)
  }
}
</script>

<style scoped lang="less">
.logo {
  width: 100px;
  height: 30px;
}
.main{
  padding-top: 46px;
  padding-bottom: 50px;
}
// 设置 tabs 容器的样式
/deep/ .van-tabs__wrap {
  padding-right: 30px;
  background-color: #fff;
}
// 设置小图标的样式
.moreChannels {
  position: fixed;
  top: 62px;
  right: 8px;
  z-index: 999;
}
// 给 popup 设置最大宽高
.channel-popup{
  width: 100vw;
  height: 100vh;
}
</style>
